      "credit_ko": "",
      "credit_en": "<p>\r\nA project by MIT Senseable City Lab<br/><br/>\r\nCarlo Ratti, Director<br/>\r\nKevin O Keeffe, Postdoctoral Fellow<br/>\r\nAmin Anjomshoaa, Visiting Scholar<br/>\r\nPaolo Santi, Project Lead<br/>\r\nFbio Duarte, Design Manager<br/>\r\nWonyoung So, Web, Visualization\r\n</p>\r\n\r\n<p>\r\nSelected as <a href=\"https://www.fastcompany.com/90372824/the-hidden-way-cabs-could-bolster-healthy-living-in-cities?partner=rss&utm_source=rss&utm_medium=feed&utm_campaign=rss+fastcompany&utm_content=rss?cid=search\" target=\"_blank\">Infographic of the Day by Fast Company</a>\r\n</p>",
