      "credit_ko": "",
      "credit_en": "<p>\r\nFrom Point A to Point B is by <a href=\"https://scalarvectortensor.net/\" target=\"_blank\">Achim Koh</a> and <a href=\"https://wonyoung.so\" target=\"_blank\">Wonyoung So</a>.<br>It was made for, exhibited at and supported by <a href=\"http://2019.datathroughdesign.com/\" target=\"_blank\">Data Through Design 2019</a>\r\n</p>",
