      "credit_en": "<p>\r\nCreated by Wonyoung So<br/><br/>\r\n\r\nPublication by<br/>\r\nWonyoung So and Fabio Duarte<br/><br/>\r\nWinner in Student Category on Information is Beautiful Awards 2019<br/><br/>\r\nSelected as an exhibition track on IEEE VIS 19 Arts Program\r\n</p>",
