      "credit_en": "<p>\r\nA project by Bell Labs<br/><br/>\r\n\r\nWonyoung So <br/>\r\nEdyta Bogucka<br/>\r\nSanja epanovi<br/>\r\nEnrique Martin-Lopez<br/>\r\nKhan Baykaner<br/>\r\nSagar Joglekar<br/>\r\nDaniele Quercia\r\n</p>",
