      "id": 30,
      "year": 2017,
      "website": "http://joins.codenamu.org/single-household/codenamu/en",
      "permalink": "affordable-housing-calculator",
      "title_ko": "Fool, The Problem is Housing: Affordable Housing Calculator",
      "title_en": "Fool, The Problem is Housing: Affordable Housing Calculator",
      "description_ko": "<p>\r\n     6 .   4 ,  20~30     ,  2    .         ?\r\n</p>\r\n",
      "description_en": "<p>\r\nFool, the problem is housing: Affordable Housing Calculator in Seoul is an application that shows where you can live in Seoul using monthly rent data. When a user inputs the current rent amount and house size (for one person, two people, or three people), it shows clearly how much one must spend to live in a particular area . In most cases, however, the amount citizens must spend to make a living is enormous. If you have not saved well, there is little chance that you will be able to live in your own house in your 20s. This applications accurate calculations reveal that it is \"almost impossible\" to live in your own home in Seoul.\r\n</p>\r\n<p>\r\nIn Korea, the Jeonse system, in which tenants pay a large deposit and the landlord makes a profit through interest, is active. It is a stepping stone for the middle class to gather money and prepare to buy a house. However, It is true that it is very difficult to live in a good house even if you save a lot of money, due to continuous Jeonse rent increases. It is an especially big problem that small, affordable houses are in poor supply because of the increase in one-person households. Seoul residents can intuitively identify the seriousness of the housing problem by calculating monthly rent, savings, and monthly salary.\r\n</p>",
