      "credit_en": "<p>\r\nJoongang Daily, Codenamu and Shared City Lab at Seoul National University<br/><br/>\r\n\r\nProducer<br/>\r\nHye Kyoung Cho<br/>\r\nSeo Hyun Shim<br/>\r\nJennifer Kang<br/>\r\nWonyoung So<br/>\r\nKyungmin Kim<br/>\r\nGoo Young Han<br/><br/>\r\n\r\nDevelopment<br/>\r\nWonyoung So<br/><br/>\r\n\r\nDesign <br/>\r\nWonyoung So\r\n</p>\r\n\r\n",
