      "credit_en": "<p>\r\nCarlo Ratti  Director<br/>\r\nPaolo Santi  Research Lead<br/>\r\nRemi Tachet  Postdoctoral Fellow<br/>\r\nFabio Duarte  Project Manager<br/>\r\nWonyoung So  Web, Visualization\r\n</p>",
