      "credit_en": "<p>\r\nCommissioned by 2017 Seoul Biennale of Architecture and Urbanism<br/><br/>\r\nCurators<br/>\r\nE Roon Kang, Wonyoung So<br/><br/>\r\nArtists<br/>\r\nMinkee Bae, Dan Phiffer, Listen to the City (Eunseon Park, Cheolhun Baek, Choonggeun Yoon, Hyunwook Jang), Hanbyul Jo<br/><br/>\r\nProject Manager<br/>\r\nHye Seong Park<br/><br/>\r\nOperation Management<br/>\r\nYoo Kyung Lee<br/><br/>\r\nDesigner<br/>\r\nMoonsick Gang<br/><br/>\r\nEditor, Translator<br/>\r\nAchim Koh<br/><br/>\r\nVideo Documentation<br/>\r\nSunshine Underground\r\n</p>",
