      "credit_en": "<p>\r\nCodenamu <br/><br/>\r\nData Visualization<br/>\r\nWonyoung So<br/><br/>\r\n\r\nData Refining<br/>\r\nHoony Jang<br/><br/>\r\n\r\nManager <br/>\r\nJeniffer Kang<br/><br/>\r\n\r\nMailing Service<br/>\r\nActivists of Code for Seoul<br/><br/>\r\n\r\nManager and Production of Crawler<br/>\r\nDong Hun Shin, Heejin Lee, Dongdong<br/><br/>\r\n\r\nManager<br/>\r\nHoony Jang<br/><br/>\r\n</p>",
